import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import Typography from 'material-ui/Typography';
import New from './New.js'

const styles = theme => ({
	link: {
    	textDecoration: 'none',
    	color: 'black',
        lineHeight: '1em',
  	},
    container: {
        margin: 15,
    },
    title: {
        fontFamily: "'Alegreya Sans', sans-serif",
        verticalAlign:'bottom',
        margin: 0,
        fontSize: 28,
    },
});

class News extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
        <div className={this.props.classes.container}>
            <Typography type="title" component="h2" className={this.props.classes.title}>
                News
            </Typography>
            <New  name={'I will serve in Taiwanese military from 1/30 to 5/18. I can not use the Internet frequently in the military but I will try my best to arrange the interview time with you. I am available on Eastern Time 2/10, 2/15~2/19, 2/24 and 2/27 to do interview.'}
                  date={'Jan 27, 2018'} />
            <New  name={'I will finish my Umbo intern on 1/29. I write a blog: Practial Deep Learning Strategy to conclude my experience.'}
                  date={'Jan 27, 2018'} />
        </div>
    );
  }
}

News.propTypes = {
   classes: PropTypes.object
}

export default withStyles(styles)(News);



