import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import Typography from 'material-ui/Typography';

const styles = theme => ({
	link: {
    	textDecoration: 'none',
        color: "#87CEEB",
        lineHeight: '1em',
  	},
    article: {
        fontSize: 18,
        fontFamily: "'Alegreya Sans', sans-serif",
        lineHeight: '1em',
    },
    container: {
        margin: 15,
    },
    title: {
        marginTop: 5,
        marginBottom: 5,
        fontFamily: "'Alegreya Sans', sans-serif",
        fontStyle: 'bold'
    },
    date:{
        fontFamily: "'Alegreya Sans', sans-serif",
    }
});

class Blog extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
        <div>
            <Typography type="subheading" component="h2" className={this.props.classes.article}>
                <a className={this.props.classes.link} href={this.props.link}>
                    {this.props.name}
                </a>
            </Typography>
            <Typography type="body1" color={'secondary'} className={this.props.classes.date}>
                {this.props.date}
            </Typography>
        </div>
    );
  }
}

Blog.propTypes = {
   classes: PropTypes.object
}

export default withStyles(styles)(Blog);



