import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import Typography from 'material-ui/Typography';

const styles = theme => ({
    article: {
        fontSize: 16,
        fontFamily: "'Alegreya Sans', sans-serif",
        lineHeight: '1em',
    },
    container: {
        margin: 15,
    },
    title: {
        marginTop: 5,
        marginBottom: 5,
        fontFamily: "'Alegreya Sans', sans-serif",
        fontStyle: 'bold'
    },
    date:{
        fontFamily: "'Alegreya Sans', sans-serif",
    }
});

class New extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
        <div>
            <Typography type="subheading" component="h2" className={this.props.classes.article}>
                {this.props.name}
            </Typography>
            <Typography type="body1" color={'secondary'} className={this.props.classes.date}>
                {this.props.date}
            </Typography>
        </div>
    );
  }
}

New.propTypes = {
   classes: PropTypes.object
}

export default withStyles(styles)(New);



