import React, { Component } from 'react';
import { withStyles } from 'material-ui/styles';
import Typography from 'material-ui/Typography';
import Grid from 'material-ui/Grid';
import Blog from './Blog.js'

const styles = {
    paper: {
        borderWidth: 1,
        borderColor: '#F0F0F0',
        borderStyle: 'solid',
        borderRadius: 6,
        padding: 6,
        backgroundColor: 'white'
    },
    title: {
        fontFamily: "'Alegreya Sans', sans-serif",
        verticalAlign:'bottom',
        margin: 0,
        fontSize: 35,
    },
    container: {
        padding: 15,
    },
}

class Blogs extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
        <div className={this.props.classes.paper}>
            <Grid container className={this.props.classes.container}>
                <Grid item xs={12} style={{paddingBottom: 0}}>
                    <Typography type="title" gutterBottom className={this.props.classes.title}>
                        <center>
                            Blogs
                        </center>
                    </Typography>
                </Grid>
                <Grid item xs={12} >
                    <Blog link={'https://medium.com/@darrenyaoyao.huang/practical-deep-learning-strategy-160fa41a4a43'}
                          name={'Practical Deep Learning Strategy'}
                          date={'Jan 29, 2018'}/>
                    <Blog link={'https://medium.com/@darrenyaoyao.huang/visualize-the-meaning-of-text-e5b1f8bcd265'}
                          name={'Visualize the Meaning of Text'}
                          date={'Jan 28, 2018'}/>
                    <Blog link={'https://medium.com/@darrenyaoyao.huang/natural-language-to-sql-d4da0e179ae4'}
                          name={'Natural Language to SQL'}
                          date={'Jan 14, 2018'} />
                    <Blog link={'https://medium.com/@darrenyaoyao.huang/attention-on-text-and-vision-39f22e0eb360'}
                          name={'Attention on Text and Vision'}
                          date={'Dec 31, 2017'} />
                    <Blog link={'https://medium.com/@darrenyaoyao.huang/ai-to-serve-humanity-d915189ae2dc'}
                          name={'Apply AI to Serving Humanity'}
                          date={'Dec 24, 2017'} />
                    <Blog link={'https://medium.com/@darrenyaoyao.huang/my-dream-comes-true-soon-combining-information-extraction-and-text-summarization-75dd630099a5'}
                          name={'My Dream Comes True Soon? Combine Information Extraction and Text Summarization'}
                          date={'Dec 16, 2017'} />
                    <Blog link={'https://medium.com/@darrenyaoyao.huang/natural-language-for-visual-2reasoning-322c36949cd1'}
                          name={'Natural Language for Visual Reasoning'}
                          date={'Dec 15, 2017'}/>
                    <Blog link={'https://medium.com/@darrenyaoyao.huang/revolution-of-video-understanding-9abb91923a4c'}
                          name={'Revolution of Video Understanding'}
                          date={'Dec 15, 2017'}/>
                    <Blog link={'https://medium.com/@darrenyaoyao.huang/do-you-ever-consider-the-gender-bias-issue-in-machine-learning-f7847b3fe1aa'}
                          name={'Have you ever considered the gender bias issue in Machine Learning?'}
                          date={'Dec 3, 2017'}/>
                </Grid>
            </Grid>
        </div>
    )
  }
}

export default withStyles(styles)(Blogs);



